.create {
    margin-left: 47px;
}

.create-btn .create-txt {

    &:hover {
        color: $backstage-primary;
    }
}

// 
.roster .roster-header {
    th {
        border-top: 0;
        border-bottom: 1px solid $backstage-dark;
        padding-bottom: 4px;
        padding-left: 0;
    }

}

.roster .roster-body {
    tr:hover {
        background: $backstage-base;
    }

    th {
        border-bottom: 1px solid $backstage-secondary;
        padding-left: 0;
        padding-top: 19px;
        padding-bottom: 17px;

    }

    td {

        border-bottom: 1px solid $backstage-secondary;
        padding-top: 19px;
        padding-bottom: 17px;
        padding-left: 0;
    }

}

// 
.pages {
    .page-item {
        .page-link {
            color: $backstage-dark;
        }

        &.active {
            .page-link {
                background-color: $backstage-primary;
                color: #fff;
            }
        }

        .page-link:hover {
            background: $backstage-primary;
            color: #fff;

        }
    }
}

.admin-account {
    font-size: 14px;

}

.warning-icon {
    margin-right: 9px;
}



//checkbox  
.custom-control-label::before {
    content: "";
    position: absolute;
    top: 0rem;
    left: -2rem;
    width: 24px;
    height: 24px;
    border: $backstage-dark solid 1px;
}
.custom-control-label::after {
    content: "";
    position: absolute;
    top: 0rem;
    left: -2rem;
    width: 24px;
    height: 24px;

}
.custom-control-input:checked~.custom-control-label::before {
    color: #fff;
    border-color: $backstage-primary;
    background-color: $backstage-primary;
}


