ul,
li {
    list-style: none;
}


.aside {
    // position: fixed;
    // top: 0;
    // bottom: 0;
    min-height:620px ;
}

.menu-item {
    position: relative;

    &:hover:before {
        content: '';
        position: absolute;
        width: 4px;
        height: 32px;
        background: $backstage-primary;
        // border-left: 4px solid $backstage-primary;
        top: -2px;
        left: -32px;
    }
}

.menu-link.text-backstage-dark:hover,
.menu-link.text-backstage-dark:focus {
    text-decoration: none;
    color: $backstage-primary !important;
}

.profile-photo {
    width: 40px;
    height: 40px;
    background: url(https://images.unsplash.com/photo-1514794749374-fb67509dbb7f?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2000&q=80);
    background-size: contain;
    border-radius: 24px;
}