.assignment-content {
    padding-top: 6px;
}

.assign-subtitle {
    margin-bottom: 6px;
}

.question-list {
    .list-group-item {
        padding: 0.75rem;
    }

    .list-group-item.active {
        background-color: $backstage-base;
        color: $backstage-dark;
        border-color: $backstage-secondary;
    }
}

.question-chapter {
    font-size: 12px;
}

.comments-num {
    font-size: 12px;

}

.askreply-header {
    padding: 0.75rem 1rem;
}

.askreply-chapter {
    font-size: 12px;
}

.askreply-title {
    margin-bottom: 0;
}

.askreply-body {
    padding: 1rem;
}

.ask-avatar {
    background: url(https://images.unsplash.com/photo-1504051771394-dd2e66b2e08f?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2134&q=80);
    background-size: contain;
    background-position: center center;
    // background-repeat: no-repeat;
    width: 40px;
    height: 40px;
    border-radius: 24px;
}

.ask-text {
    font-size: 14px;
}

.code-tag {
    font-family: Roboto Mono;
}

.reply-avatar {
    background: url(https://images.unsplash.com/photo-1514794749374-fb67509dbb7f?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2000&q=80);
    background-size: contain;
    background-position: center center;
    // background-repeat: no-repeat;
    width: 40px;
    height: 40px;
    border-radius: 24px;
}
.next{
    font-size: 24px;
}
